import React from 'react';
import bg from "../Assets/Images/bg.png";
import Navbar from './Navbar';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

function Register() {
    const containerStyle = {
        backgroundImage: `url(${bg})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
    };

    return (
        <div
            style={containerStyle}
            className="flex flex-col items-center h-screen py-12 px-12"
        >
            <div className="border rounded-lg bg-opacity-19 backdrop-blur-[14.616px] h-[90vh] md:w-[80vw] max-w-[1376px] px-5 py-5 flex flex-col gap-12">
                <div>
                    <Navbar />
                </div>

                <div className='flex justify-center  '>
                    <div className="bg-opacity-19 h-auto md:w-auto px-32 py-5 flex flex-col rounded-3xl bg-opacity-20 bg-gray-300 shadow-2xl backdrop-blur-md">
                        <div className='flex items-center flex-col gap-4'>
                        <div className='flex items-center flex-col gap-3'>
                            <div className='text-2xl font-bold'>Register</div>
                            <div className='text-lg text-[#606060]'>Welcome to Grandeur</div>
                        </div>

                        {/* Login Form */}
                        <form >
                            <div className='flex flex-col gap-2 justify-center items-center'>
                                <TextField
                                    label="Email"
                                    variant="standard"
                                    margin="normal"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />
                                <TextField
                                    label="Password"
                                    variant="standard"
                                    margin="normal"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    type="password"
                                />
                                <div className='w-full'>
                                    <Button
                                        type="submit"
                                        variant="contained"
                                        fullWidth
                                        style={{ backgroundColor: 'rgba(0, 0, 0, 0.70)', color: 'white', fontSize:"12px" }}
                                    >
                                        Register
                                    </Button>
                                </div>
                               
                            </div>
                        </form>

                       <div className='flex justify-center items-center text-sm text-[#0000008A] gap-2' style={{width:"524px"}}>
                       Your personal data will be used to support your experience throughout this website, to manage access to your account, 
                       and for other purposes described in our privacy policy.
                       </div>
                       <div className='flex justify-center items-center gap-2 '>
                       Already have an account? 
    <a className='text-blue-700 text-sm underline ' href='/login'> log in</a>

           </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    );
}

export default Register;
