import React from "react";
import logo2 from "../Assets/Images/logo2.png";
import watch from "../Assets/Images/Titanium_Jet_Black_New_Case_StrapBlack-5.png.png";
import { useNavigate } from "react-router-dom";
function Main() {
  const navigate = useNavigate();
  const handleclick = () => {
    navigate("/watches");
  };
  return (
    <div className="flex flex-col md:flex-row justify-between items-center w-full px-5 md:px-5 py-10">
      <div className="flex flex-col gap-2 lg:gap-4">
        <img src={logo2} className="w-[300px] md:w-[560px]" alt="Logo" />
        <div className="text-md md:text-lg font-semibold">
          Build a Watch on Your Style
        </div>
        <div>Starts from $380</div>
        <button
          onClick={handleclick}
          className="rounded-full bg-black bg-opacity-50 text-white w-fit px-3 py-1 md:px-4 md:py-2"
        >
          Get Started
        </button>
      </div>
      <div>
        <img
          src={watch}
          className="h-[210px] md:h-[230px] w-[200px] md:w-[390px] lg:h-[290px] lg:w-[290px]"
          alt="Watch"
        />
      </div>
    </div>
  );
}

export default Main;
