import React from "react";
import { Routes, Route } from "react-router-dom";
import LandingPage from "./Components/LandingPage";
import Watches from "./Components/Watches";
import Register from "./Components/Register";
import Login from "./Components/Login";
import Forget from "./Components/Forget";

function MyRoutes() {
  return (
    <Routes>
      <Route path="/" element={<LandingPage />} />
      <Route path="/watches" element={<Watches />} /> {/* Define the route */}
      <Route path="/login" element={<Login />} /> {/* Define the route */}
      <Route path="/register" element={<Register />} /> {/* Define the route */}
      <Route path="/forget" element={<Forget />} /> {/* Define the route */}
    </Routes>
  );
}

export default MyRoutes;
