import React from "react";
import logo from "../Assets/Images/logo.png";
import { IoSearchOutline, IoCart } from "react-icons/io5";
function Navbar() {
  return (
    <div>
      <div className="flex items-center justify-between">
        <img src={logo} alt="Logo" className="h-8" />
        <ul className="flex space-x-6 list-none">
          <li className="cursor-pointer">Premium Watches</li>
          <span className="w-0.5 h-auto bg-black opacity-[0.28]"></span>
          <li className="cursor-pointer">NewsLetters</li>
          <span className="w-0.5 h-auto bg-black opacity-[0.28]"></span>
          <li className="cursor-pointer">FAQs</li>
          <span className="w-0.5 h-auto bg-black opacity-[0.28]"></span>
          <li className="cursor-pointer">Contact Us</li>
        </ul>
        <div className="flex space-x-6 items-center">
          <IoSearchOutline />
          <IoCart />
          <button className="rounded-lg bg-black text-white px-3 py-1">
            Login/Register
          </button>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
